/**
 * Site header
 */
.first-page {

    margin-top:50px ;
    margin-bottom:5px;
    vertical-align:middle;
    text-align:center;

    > h1 {
      font-size: $first-font-size;
      text-shadow: 4px 4px 4px #aaa;
    }
}

//.first-page-trigger {
//    clear: both;
//    display: none;
//}

.copyright {
    text-align:center;
    margin-top:10px ;
    margin-bottom:10px;
}

.copyright-first-page {
    position: fixed;
    bottom: 10px;
    width: 100%;
    border-top: 1px solid $grey-color-light;
    padding-top:10px ;
}

/**
 * Site header
 */
.site-header {
  //  border-bottom: 1px solid $grey-color-light;
    min-height: 56px;

    // Positioning context for the mobile navigation icon
    position: relative;

}

.first-page-header {
    border-top: 2px solid $grey-color-dark;
  //  border-bottom: 1px solid $grey-color-light;
    min-height: 56px;

    // Positioning context for the mobile navigation icon
    position: relative;
}

.site-title {
    font-size: 27px;
    line-height: 56px;
    letter-spacing: -1px;
    margin-bottom: 0;
    float: left;
    text-shadow: 2px 2px 2px #aaa;

    &,
    &:visited {
        color: $grey-color-dark;
    }
}

.first-page-link {
    color: $text-color;
    line-height: $base-line-height;
    text-shadow: 2px 2px 2px #aaa;

    &:visited {
      color: $brand-color;
    }

    &:after {
      color: $text-color;
      content: '-';
      margin-left: 15px;
      margin-right: 15px;
    }

    &:first-child:after {
      content: '-';
      margin-left: 15px;
      margin-right: 15px;
    }

    &:last-child:after {
      content: '';
      margin-left: 0px;
      margin-right: 0px;
    }

}

.site-nav {
    float: right;
    line-height: 56px;

    .menu-icon {
        display: none;
    }

    .page-link {
        color: $text-color;
        line-height: $base-line-height;

        // Gaps between nav items, but not on the first one
        &:not(:first-child) {
            margin-left: 20px;
        }
    }

    @include media-query($on-palm) {
        position: absolute;
        top: 9px;
        right: 30px;
        background-color: $background-color;
        border: 1px solid $grey-color-light;
        border-radius: 5px;
        text-align: right;

        .menu-icon {
            display: block;
            float: right;
            width: 36px;
            height: 26px;
            line-height: 0;
            padding-top: 10px;
            text-align: center;

            > svg {
                width: 18px;
                height: 15px;

                path {
                    fill: $grey-color-dark;
                }
            }
        }

        .trigger {
            clear: both;
            display: none;
        }

        &:hover .trigger {
            display: block;
            padding-bottom: 5px;
        }

        .page-link {
            display: block;
            padding: 5px 10px;
        }
    }
}



/**
 * Site footer
 */
.site-footer {
    border-top: 1px solid $grey-color-light;
    padding: $spacing-unit 0;
}

.footer-heading {
    font-size: 18px;
    margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
    list-style: none;
    margin-left: 0;
}

.footer-col-wrapper {
    text-align: center;
    font-size: 15px;
    color: $grey-color;
    margin-left: -$spacing-unit / 2;
    @extend %clearfix;
}

.footer-col {
    float: left;
    margin-bottom: $spacing-unit / 2;
    padding-left: $spacing-unit / 2;
}

.footer-col-1 {
    width: -webkit-calc(35% - (#{$spacing-unit} / 2));
    width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
    width: -webkit-calc(20% - (#{$spacing-unit} / 2));
    width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
    width: -webkit-calc(45% - (#{$spacing-unit} / 2));
    width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
    .footer-col-1,
    .footer-col-2 {
        width: -webkit-calc(50% - (#{$spacing-unit} / 2));
        width:         calc(50% - (#{$spacing-unit} / 2));
    }

    .footer-col-3 {
        width: -webkit-calc(100% - (#{$spacing-unit} / 2));
        width:         calc(100% - (#{$spacing-unit} / 2));
    }
}

@include media-query($on-palm) {
    .footer-col {
        float: none;
        width: -webkit-calc(100% - (#{$spacing-unit} / 2));
        width:         calc(100% - (#{$spacing-unit} / 2));
    }

    .first-page {

        margin-top:20px ;
        margin-bottom:2px;

        > h1 {
          font-size: 9.6vw;
          text-shadow: 2px 2px 2px #aaa;
        }
    }

    .first {
      font-size: 70%;
    }

    .site-title {
        font-size: 4.5vw;
    }

    .copyright-first-page {
        font-size: 2.5vw;
    }
}



/**
 * Page content
 */
.page-content {
  //  padding: $spacing-unit 0;
  padding: 0 0;

    @include media-query($on-palm) {
      padding: $spacing-unit/4 0;
    }
}

.page-heading {
    font-size: 20px;
}

.post-list {
    margin-top: $spacing-unit*2;
    margin-left: 0;
    list-style: none;
}

.post-list-item {
    margin-bottom: $spacing-unit*2;
    border-bottom:     1px dashed $grey-color;

    @include media-query($on-palm) {
      margin-bottom: $spacing-unit;
    }
}

.post-meta {
    font-size: $small-font-size;
    color: $grey-color;
}

.post-link {
    display: block;
    font-size: 24px;
}


.archive-per-date, .archive-per-tag {

}

.archive {
  border:     1px solid $grey-color-light;
  padding:    10px;
  font-size:  70%;
}


/**
 * Posts
 */
.post-header {
    margin-bottom: $spacing-unit/2;
    margin-top: 0;
    padding-bottom: $spacing-unit/4;
    padding-top: $spacing-unit/4;

    @include media-query($on-palm) {
        margin-bottom: 0;
    }
}


.post-title {
    font-size: 42px;
    letter-spacing: -1px;
    line-height: 1;

    @include media-query($on-laptop) {
        font-size: 36px;
    }
}

.post-content {
    margin-top: $spacing-unit*2;
    margin-bottom: $spacing-unit*2;

    h1 {
      font-size: 32px;

      @include media-query($on-laptop) {
          font-size: 30px;
      }
    }

    h2 {
        font-size: 28px;

        @include media-query($on-laptop) {
            font-size: 28px;
        }
    }

    h3 {
        font-size: 26px;

        @include media-query($on-laptop) {
            font-size: 22px;
        }
    }

    h4 {
        font-size: 24px;

        @include media-query($on-laptop) {
            font-size: 18px;
        }
    }
}

.post-social {
    margin-bottom: $spacing-unit;
}




