@import url(https://fonts.googleapis.com/css?family=Anaheim);
@import url(https://fonts.googleapis.com/css?family=Cutive+Mono);
@import url(https://fonts.googleapis.com/css?family=Tangerine:700);
@import url(https://fonts.googleapis.com/css?family=Petit+Formal+Script&subset=latin,latin-ext);


/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

/**
 * Basic styling
 */
body {
    font-family: $base-font-family;
    font-size: $base-font-size;
    line-height: $base-line-height;
    font-weight: 400;
    color: $text-color;
    background-color: $background-color;
    -webkit-text-size-adjust: 100%;
}

thead {
  background-color: $color-blog;
}

td {
  padding: 0px 10px 0px 10px;

}

tbody {
  /**> tr:nth-child(even) {
    background: #CCC;
  }

  > tr:nth-child(odd) {
    background: #FFF;
  }*/
}

/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}

p {
  line-height: 150%;
  margin-bottom: $spacing-unit;
  text-align: justify;
  text-justify: inter-word;
}

/**
 * Images
 */
img {
  //  max-width: 100%;
    vertical-align: middle;
    max-width:80%; 
    display: block; 
    margin-left: auto; 
    margin-right: auto;
}

.flag {
    display:inline;
}

/**
 * Figures
 */
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
         margin-bottom: 0;
    }
}


/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
}

h1.post-title {
  background-color: rgba(254, 254, 254, 0.15);
  //background-color: #A1B56C;
  color: rgba(255, 255, 255, 1);

  border-radius: 8px;
  padding: 6px 16px 6px 16px;
  margin: 8px 8px 8px 8px;
  display: inline-block;
}

/**
 * Links
 */
a {
    color: $brand-color;
    text-decoration: none;

    &:visited {
        color: darken($brand-color, 15%);
    }

    &:hover {
        color: $text-color;
        text-decoration: none;
    }
}



/**
 * Blockquotes
 */
blockquote {
    color: $grey-color;
    border-left: 4px solid $grey-color-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    letter-spacing: 0px;
    font-style: italic;
    font-family: $base-quote-font-family;
    text-shadow: 1px 1px 1px #aaa;

    > :last-child {
        margin-bottom: 0;
    }
}

.info, .notice, .error, .warn {
  font-size: 95%;
  letter-spacing: 0px;
  padding-left: $spacing-unit / 2;
  font-style:     italic;
  padding-top:    7px;
  padding-bottom: 7px;
  margin-top:     24px;
  margin-bottom:  24px;
}

.info {
  border-left: 8px solid $color-apropos;
  background-color: $color-apropos-t;

}

.notice {
  border-left: 8px solid $color-blog;
  background-color: $color-blog-t;
}

.warn {
  border-left: 6px solid $color-contact;
  background-color: $color-contact-t;

}

.error {
  border-left: 6px solid $color-e404;
  background-color: $color-e404-t;

}

div.author {
    text-align: right;
    font-style: normal;
    font-size: 16px;
    margin-bottom: $spacing-unit ;
}

div.author:before {
    content: "-";
    margin-right: 5px;

}

div.author:after {
    content: "-";
    margin-left: 5px;

}




/**
 * Code formatting
 */
pre,
code {
    font-size: 12.5px;
    border: 1px solid $grey-color-light;
    border-radius: 0px;
    background-color: #dfdfdf;
    font-family: $base-mono-font-family;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: scroll;
    border-radius: 0px;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}



/**
 * Wrapper
 */
.wrapper {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
    max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
        max-width:         calc(#{$content-width} - (#{$spacing-unit}));
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}



/**
 * Clearfix
 */
%clearfix {

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
 * Icons
 */
.icon {

    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $grey-color;
        }
    }
}
