@charset "utf-8";


// Our variables
$base-font-family: Anaheim, Helvetica,Arial,sans-serif;
$base-font-size:   15px;
$first-font-size:  58px;
$small-font-size:  $base-font-size * 0.90;
$base-line-height: 1.33;

$spacing-unit:     20px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #2a7ae2;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

$base-mono-font-family: 'Cutive Mono';
$base-quote-font-family: 'Petit+Formal+Script';

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;


$color-apropos:   #6fb3d2;
$color-blog:      #76c7b7;
//$color-citation:  #fc6d24;
$color-citation:  #fd874b;
//$color-citation:  #fd7632;
$color-contact:   #fda331;
$color-gpg:       #d381c3;
$color-gpg:       #d890ca;
$color-e404:      #fb0120;
$color-diver:     #be643c;

$color-apropos-t:   rgba(111, 179, 210, 0.5);
$color-blog-t:      rgba(118, 199, 183, 0.5);
//$color-citation:  #fc6d2477;
//$color-citation-t:  rgba(111, 179, 210, 0.5);
//$color-citation:  #fd763277;
$color-contact-t:   rgba(253, 163, 49, 0.5);
//$color-gpg-t:       #d381c3;
//$color-gpg-t:       rgba(111, 179, 210, 0.45);
$color-e404-t:      rgba(251, 1, 32, 0.5);
//$color-diver-t:     rgba(111, 179, 210, 0.45);


